import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import { useRecoilState, useRecoilValue, useRecoilValueLoadable, useSetRecoilState } from 'recoil';
import { getMoviesQuery, test, testSelector } from '@recoil/selectors/movieSelector';
import { moviePage, movieState } from '@recoil/atoms/movieAtom';
import { v4 as uuidv4 } from 'uuid';
import useInfinityScroll from '@hooks/useInfinityScroll';

const Content = () => {
  const movies = useRecoilValue(movieState);
  const [page, setPage] = useRecoilState(moviePage);
  const [curMovies, setMovie] = useRecoilState(getMoviesQuery);

  const onIntersect: IntersectionObserverCallback = ([{ isIntersecting }]) => {
    if (isIntersecting) {
      console.log(1);
    }
  };

  const setTarget = useInfinityScroll({ onIntersect, threshold: 0 });

  return (
    <article className="h-[200px] bg bg-red-100 overflow-y-auto">
      <button
        onClick={() => {
          setPage(page + 1);
          setMovie(curMovies);
        }}
      >
        click
      </button>
      <ul>
        {movies.map((v, i) => {
          if (i === 10 * (page - 1)) {
            return (
              <li ref={setTarget} key={uuidv4()}>
                {v.title}
              </li>
            );
          } else {
            return <li key={uuidv4()}>{v.title}</li>;
          }
        })}
      </ul>
    </article>
  );
};

export default Content;
