import { moviePage, movieState } from './../atoms/movieAtom';
import { http } from '../../axiosInstance';
import { selector, atom, DefaultValue } from 'recoil';
import { Movie, Response } from 'types';

export const getMoviesQuery = selector<Movie[]>({
  key: 'getMoviesQuery',
  get: async ({ get }) => {
    try {
      const { data } = await http.get<Response<Movie[]>>('/movie/popular', {
        params: {
          page: get(moviePage),
          api_key: process.env.KEY,
        },
      });

      return data.results || [];
    } catch (err) {
      throw 'movie data error';
    }
  },
  set: ({ set, get }, curMovies) => {
    const preMovies = get(movieState);
    if (!(curMovies instanceof DefaultValue)) {
      set(movieState, [...preMovies, ...curMovies]);
    }
  },
});

export const test = atom({
  key: 'test',
  default: 1,
});

export const testSelector = selector<number>({
  key: 'testSelector',
  get: async ({ get }) => {
    const cur = get(test);
    const next = cur + 1;
    return next;
  },
  set: ({ set }, cur) => {
    console.log('set', cur);
    return set(test, cur);
  },
});
